.app {
  display: flex;
  height: 100vh;
}

.map {
  flex: 3;
  display: grid;
  grid-template-columns: repeat(20, 40px); /* 20 колонок по 40px */
  grid-template-rows: repeat(15, 40px);    /* 15 строк по 40px */
  gap: 1px;                                /* Расстояние между ячейками */
  background: grey;                        /* Временный фон */
  position: relative;
  border: 1px solid #333;                  /* Граница карты */
}

.token {
  position: absolute;
  width: 40px;
  height: 40px;
  background: red;
  color: white;
  text-align: center;
  cursor: move;
  z-index: 10;                             /* Чтобы токены были поверх сетки */
}

.chat {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.messages {
  flex: 1;
  overflow-y: auto;
}